import { DefaultProps, Flex as MantineFlex, FlexProps as MantineFlexProps } from '@mantine/core';
import { CSSProperties, FC, PropsWithChildren, forwardRef } from 'react';
import { Size, TwBaseProps } from '../../types';
import { styled } from '../../utils';

export interface FlexProps
  extends TwBaseProps,
    PropsWithChildren,
    Omit<MantineFlexProps, keyof DefaultProps> {
  gap?: Size;
  rowGap?: Size;
  columnGap?: Size;
  align?: CSSProperties['alignItems'];
  justify?: CSSProperties['justifyContent'];
  wrap?: CSSProperties['flexWrap'];
  direction?: CSSProperties['flexDirection'];
}

const Root = styled(MantineFlex)<MantineFlexProps>(() => ({}));

export const Flex: FC<FlexProps> = forwardRef((props, ref) => {
  return <Root {...props} ref={ref} />;
});
